<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="AddEdit"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">
            <span v-if="status">新增公司</span><span v-else>修改公司</span>
          </h1>
          <button type="button" class="btn-close" @click="hideModal" aria-label="Close"></button>
        </div>
        <VForm
          ref="formRefSignin"
          id="profileForm"
          v-slot="{ errors, meta }"
          @submit="addEditCompany"
        >
          <div class="modal-body">
            <div class="form-floating mb-3">
              <VField
                name="廠商"
                type="text"
                rules="required"
                class="form-control"
                v-model="tempData.companyName"
              />
              <label for="floatingInput"
                >廠商名稱<ErrorMessage name="廠商" class="text-danger ms-3"
              /></label>
            </div>
            <div class="form-floating mb-3">
              <VField
                name="負責人"
                type="text"
                rules="required"
                class="form-control"
                v-model="tempData.name"
              />
              <label for="floatingInput"
                >負責人名稱<ErrorMessage name="負責人" class="text-danger ms-3"
              /></label>
            </div>
            <div class="form-floating mb-3">
              <VField
                id="phone"
                name="phone"
                type="text"
                class="form-control"
                :class="{ 'is-invalid': errors['電話'] }"
                :rules="isPhone"
                v-model="tempData.phone"
              />
              <label for="phone"
                >phone<error-message name="phone" class="text-danger ms-3"></error-message
              ></label>
            </div>
            <div class="form-floating mb-3">
              <VField
                name="統一編號"
                class="form-control"
                type="text"
                :rules="{ regex: /^[0-9]{8}$/ }"
                v-model="tempData.taxIdNumber"
              />
              <label for="floatingInput"
                >統一編號<ErrorMessage name="統一編號" class="ms-3 text-danger"
              /></label>
            </div>
            <div class="form-floating mb-3">
              <VField name="地址" type="text" v-model="tempData.address" class="form-control" />
              <label for="floatingInput"
                >地址<ErrorMessage name="地址" class="text-danger ms-3"
              /></label>
            </div>
          </div>
          <div class="modal-footer justify-content-between">
            <button
              type="button"
              class="btn btn-outline-danger"
              v-if="!status"
              @click="deleteCompany"
            >
              刪除廠商資料
            </button>
            <div></div>
            <button type="submit" class="btn btn-primary" :disabled="!meta.valid">儲存</button>
          </div>
        </VForm>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import { apiPostCompany, apiPatchCompany, apiDeleteCompany } from '@/assets/javascript/api';

export default {
  data() {
    return {
      modal: '',
      status: false,
      tempData: {
        companyName: '',
        name: '',
        phone: '',
        taxIdNumber: '',
        address: '',
      },
    };
  },
  methods: {
    isPhone(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : '需要正確的電話號碼';
    },
    addEditCompany() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-underscore-dangle
      if (this.tempData._id) {
        apiPatchCompany(this.tempData)
          .then(() => {
            // alert成功訊息
            this.$emitter.emit('messageModal', { status: true, message: '修改成功' });
            // 重新取得資料
            this.$emitter.emit('getCompanyLsit');
            // 結束loading效果
            this.$emitter.emit('loadingStatus', false);
            // 關閉modal
            this.hideModal();
          })
          .catch((err) => {
            this.$emitter.emit('messageModal', {
              status: false,
              message: err.response.data.message,
            });
            this.$emitter.emit('loadingStatus', false);
          });
      } else {
        apiPostCompany(this.tempData)
          .then(() => {
            this.$emitter.emit('messageModal', { status: true, message: '新增成功' });
            this.$emitter.emit('getCompanyLsit');
            this.$emitter.emit('loadingStatus', false);
            this.hideModal();
          })
          .catch((err) => {
            this.$emitter.emit('messageModal', {
              status: false,
              message: err.response.data.message,
            });
            this.$emitter.emit('loadingStatus', false);
          });
      }
    },
    deleteCompany() {
      // eslint-disable-next-line no-alert, no-underscore-dangle
      const id = prompt(`輸入該廠商ＩＤ：${this.tempData._id}`, '');
      // eslint-disable-next-line no-underscore-dangle
      if (id === this.tempData._id) {
        // eslint-disable-next-line no-underscore-dangle
        apiDeleteCompany(this.tempData._id)
          .then(() => {
            this.$emitter.emit('messageModal', { status: true, message: '刪除成功' });
            this.$emitter.emit('getCompanyLsit');
            this.$emitter.emit('loadingStatus', false);
            this.hideModal();
          })
          .catch((err) => {
            this.$emitter.emit('messageModal', {
              status: false,
              message: err.response.data.message,
            });
            this.$emitter.emit('loadingStatus', false);
          });
      }
    },
    // async uploadImage() {
    //   const formData = new FormData(document.getElementById('profileForm'));
    //   await this.upload(formData, 'photo');
    //   this.tempProfile.photo = this.tempImage.url;
    //   this.imageMsg = this.tempImage.msg;
    // },
    showModal() {
      this.modal.show();
    },
    hideModal() {
      this.tempData = {};
      this.modal.hide();
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.AddEdit);
    this.$emitter.on('addEditCom', (e) => {
      this.status = e.status;
      if (!e.status) {
        this.tempData = JSON.parse(JSON.stringify(e.data));
      }
      this.showModal();
    });
  },
};
</script>
