<template>
  <Navbar />
  <section class="container-xl">
    <router-view />
  </section>
  <Loading />
  <Message />
  <WorkRecordModal />
  <AddEditCompany />
  <AddEditOrder />
  <AddEditWorker />
  <PDF />
  <EditOrder />
  <CooperateModal />
</template>

<style lang="scss">
@import '@/assets/stylesheets/all';
</style>

<script>
import Navbar from '@/components/layout/NavbarModel.vue';
import Loading from '@/components/LoadingModal.vue';
import Message from '@/components/MessageModal.vue';
import { getToken, clearToken } from '@/assets/javascript/methods';
import WorkRecordModal from './components/workerModal/WorkRecordModal.vue';
import PDF from './components/orderModal/PDF.vue';
import AddEditCompany from './components/companyModal/AddEditCompany.vue';
import AddEditOrder from './components/orderModal/AddEditOrder.vue';
import AddEditWorker from './components/workerModal/AddEditWorker.vue';
import EditOrder from './components/orderModal/EditOrder.vue';
import CooperateModal from './components/companyModal/CooperateModal.vue';

export default {
  components: {
    Loading,
    Message,
    Navbar,
    WorkRecordModal,
    AddEditCompany,
    AddEditOrder,
    AddEditWorker,
    PDF,
    EditOrder,
    CooperateModal,
  },
  methods: {
    // 確認登入狀態
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();

      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/login');
      }
    },
    // 登出
    signOut() {
      clearToken();
      this.$router.push('/signin');
    },
  },
  created() {
    this.checkSignIn();
  },
};
</script>
