<script>
import { Modal } from 'bootstrap';
import { apiGetCooperateList, apiGetOrder } from '@/assets/javascript/api';

export default {
  data() {
    return {
      modal: '',
      tempData: '',
      tempOrder: '',
    };
  },
  methods: {
    getOrder(id) {
      this.$emitter.emit('loadingStatus', true);
      apiGetOrder(id)
        .then((res) => {
          this.$emitter.emit('loadingStatus', false);
          this.tempOrder = res.data.data;
        })
        .catch((err) => {
          this.$emitter.emit('loadingStatus', false);
          console.log(err);
        });
    },
    getCooperList(id) {
      this.$emitter.emit('loadingStatus', true);
      apiGetCooperateList(id)
        .then((res) => {
          this.$emitter.emit('loadingStatus', false);
          this.tempData = res.data.data;
        })
        .catch((err) => {
          this.$emitter.emit('loadingStatus', false);
          console.log(err.response.data.message);
        });
    },
    showModal() {
      this.modal.show();
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.cooperate);
    this.$emitter.on('cooperList', (e) => {
      this.getCooperList(e);
      this.showModal();
    });
  },
};
</script>
